'use client'

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import { Checkbox } from '@/components/ui/checkbox'

interface Student {
  id: number
  fullName: string
  level: string
  cin: string
}

interface AttendanceRecord {
  id: number
  studentId: number
  studentName: string
  date: string
  present: boolean
  reason?: string
}

interface AttendanceStats {
  totalStudents: number
  presentToday: number
  absentToday: number
  attendanceRate: number
}

export default function AttendancePage() {
  const [currentLang, setCurrentLang] = useState('fr')
  const [students, setStudents] = useState<Student[]>([])
  const [attendanceRecords, setAttendanceRecords] = useState<AttendanceRecord[]>([])
  const [selectedDate, setSelectedDate] = useState(new Date().toISOString().split('T')[0])
  const [selectedLevel, setSelectedLevel] = useState('')
  const [loading, setLoading] = useState(true)
  const [stats, setStats] = useState<AttendanceStats>({
    totalStudents: 0,
    presentToday: 0,
    absentToday: 0,
    attendanceRate: 0
  })

  // Attendance form state
  const [attendanceForm, setAttendanceForm] = useState<{[key: number]: {present: boolean, reason: string}}>({})

  useEffect(() => {
    fetchStudents()
    fetchAttendanceData()
  }, [])

  useEffect(() => {
    if (selectedDate) {
      fetchAttendanceData()
    }
  }, [selectedDate, selectedLevel])

  const fetchStudents = async () => {
    try {
      const response = await fetch('/api/students')
      if (response.ok) {
        const data = await response.json()
        setStudents(data.students || [])
        
        // Initialize attendance form
        const initialForm: {[key: number]: {present: boolean, reason: string}} = {}
        data.students.forEach((student: Student) => {
          initialForm[student.id] = { present: true, reason: '' }
        })
        setAttendanceForm(initialForm)
      }
    } catch (error) {
      console.error('Error fetching students:', error)
    }
  }

  const fetchAttendanceData = async () => {
    try {
      // Mock attendance data for demonstration
      const mockAttendance: AttendanceRecord[] = [
        {
          id: 1,
          studentId: 1,
          studentName: 'Ahmed Benali',
          date: selectedDate,
          present: true,
          reason: ''
        },
        {
          id: 2,
          studentId: 2,
          studentName: 'Fatima Alami',
          date: selectedDate,
          present: false,
          reason: 'Maladie'
        },
        {
          id: 3,
          studentId: 3,
          studentName: 'Omar Tazi',
          date: selectedDate,
          present: true,
          reason: ''
        },
        {
          id: 4,
          studentId: 4,
          studentName: 'Aicha Bennani',
          date: selectedDate,
          present: false,
          reason: 'Rendez-vous médical'
        }
      ]

      setAttendanceRecords(mockAttendance)
      
      // Calculate stats
      const totalStudents = students.length || mockAttendance.length
      const presentToday = mockAttendance.filter(record => record.present).length
      const absentToday = mockAttendance.filter(record => !record.present).length
      const attendanceRate = totalStudents > 0 ? (presentToday / totalStudents) * 100 : 0

      setStats({
        totalStudents,
        presentToday,
        absentToday,
        attendanceRate: Math.round(attendanceRate)
      })
    } catch (error) {
      console.error('Error fetching attendance data:', error)
    } finally {
      setLoading(false)
    }
  }

  const handleAttendanceChange = (studentId: number, field: 'present' | 'reason', value: boolean | string) => {
    setAttendanceForm(prev => ({
      ...prev,
      [studentId]: {
        ...prev[studentId],
        [field]: value
      }
    }))
  }

  const handleSaveAttendance = async () => {
    try {
      // In real app, send to API
      const attendanceData = Object.entries(attendanceForm).map(([studentId, data]) => ({
        studentId: parseInt(studentId),
        date: selectedDate,
        present: data.present,
        reason: data.reason
      }))

      console.log('Saving attendance:', attendanceData)
      
      alert(currentLang === 'fr' ? 'Présences enregistrées avec succès!' : 'تم حفظ الحضور بنجاح!')
      fetchAttendanceData()
    } catch (error) {
      console.error('Error saving attendance:', error)
      alert(currentLang === 'fr' ? 'Erreur lors de l\'enregistrement' : 'خطأ في الحفظ')
    }
  }

  const levels = [
    { value: 'cp', label: currentLang === 'fr' ? 'CP (Cours Préparatoire)' : 'التحضيري' },
    { value: 'ce1', label: currentLang === 'fr' ? 'CE1' : 'الأول ابتدائي' },
    { value: 'ce2', label: currentLang === 'fr' ? 'CE2' : 'الثاني ابتدائي' },
    { value: 'cm1', label: currentLang === 'fr' ? 'CM1' : 'الثالث ابتدائي' },
    { value: 'cm2', label: currentLang === 'fr' ? 'CM2' : 'الرابع ابتدائي' },
    { value: '6eme', label: currentLang === 'fr' ? '6ème' : 'السادس ابتدائي' },
    { value: '5eme', label: currentLang === 'fr' ? '5ème' : 'الأول إعدادي' },
    { value: '4eme', label: currentLang === 'fr' ? '4ème' : 'الثاني إعدادي' },
    { value: '3eme', label: currentLang === 'fr' ? '3ème' : 'الثالث إعدادي' },
  ]

  const filteredStudents = selectedLevel 
    ? students.filter(student => student.level === selectedLevel)
    : students

  const filteredAttendance = selectedLevel
    ? attendanceRecords.filter(record => {
        const student = students.find(s => s.id === record.studentId)
        return student?.level === selectedLevel
      })
    : attendanceRecords

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-green-600 mx-auto mb-4"></div>
          <p className="text-gray-600">
            {currentLang === 'fr' ? 'Chargement...' : 'جاري التحميل...'}
          </p>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen py-8">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-800 mb-2">
            {currentLang === 'fr' ? 'Suivi des Présences' : 'متابعة الحضور'}
          </h1>
          <p className="text-gray-600">
            {currentLang === 'fr' 
              ? 'Gestion des présences et absences des élèves'
              : 'إدارة حضور وغياب الطلاب'
            }
          </p>
        </div>

        {/* Date and Level Filters */}
        <Card className="mb-8">
          <CardHeader>
            <CardTitle>
              {currentLang === 'fr' ? 'Filtres' : 'المرشحات'}
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="flex flex-col md:flex-row gap-4">
              <div className="flex-1">
                <Label htmlFor="date">
                  {currentLang === 'fr' ? 'Date' : 'التاريخ'}
                </Label>
                <Input
                  id="date"
                  type="date"
                  value={selectedDate}
                  onChange={(e) => setSelectedDate(e.target.value)}
                  className="mt-1"
                />
              </div>
              <div className="flex-1">
                <Label htmlFor="level">
                  {currentLang === 'fr' ? 'Niveau' : 'المستوى'}
                </Label>
                <Select value={selectedLevel} onValueChange={setSelectedLevel}>
                  <SelectTrigger className="mt-1">
                    <SelectValue placeholder={
                      currentLang === 'fr' ? 'Tous les niveaux' : 'جميع المستويات'
                    } />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="">
                      {currentLang === 'fr' ? 'Tous les niveaux' : 'جميع المستويات'}
                    </SelectItem>
                    {levels.map((level) => (
                      <SelectItem key={level.value} value={level.value}>
                        {level.label}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Stats Cards */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
          <Card className="border-0 shadow-lg">
            <CardContent className="p-6">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm font-medium text-gray-600">
                    {currentLang === 'fr' ? 'Total Élèves' : 'إجمالي الطلاب'}
                  </p>
                  <p className="text-2xl font-bold text-blue-600">{stats.totalStudents}</p>
                </div>
                <div className="w-12 h-12 bg-blue-100 rounded-lg flex items-center justify-center">
                  <span className="text-blue-600 font-bold">👥</span>
                </div>
              </div>
            </CardContent>
          </Card>

          <Card className="border-0 shadow-lg">
            <CardContent className="p-6">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm font-medium text-gray-600">
                    {currentLang === 'fr' ? 'Présents' : 'حاضر'}
                  </p>
                  <p className="text-2xl font-bold text-green-600">{stats.presentToday}</p>
                </div>
                <div className="w-12 h-12 bg-green-100 rounded-lg flex items-center justify-center">
                  <span className="text-green-600 font-bold">✅</span>
                </div>
              </div>
            </CardContent>
          </Card>

          <Card className="border-0 shadow-lg">
            <CardContent className="p-6">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm font-medium text-gray-600">
                    {currentLang === 'fr' ? 'Absents' : 'غائب'}
                  </p>
                  <p className="text-2xl font-bold text-red-600">{stats.absentToday}</p>
                </div>
                <div className="w-12 h-12 bg-red-100 rounded-lg flex items-center justify-center">
                  <span className="text-red-600 font-bold">❌</span>
                </div>
              </div>
            </CardContent>
          </Card>

          <Card className="border-0 shadow-lg">
            <CardContent className="p-6">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm font-medium text-gray-600">
                    {currentLang === 'fr' ? 'Taux de Présence' : 'معدل الحضور'}
                  </p>
                  <p className="text-2xl font-bold text-purple-600">{stats.attendanceRate}%</p>
                </div>
                <div className="w-12 h-12 bg-purple-100 rounded-lg flex items-center justify-center">
                  <span className="text-purple-600 font-bold">📊</span>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Main Content */}
        <Tabs defaultValue="mark" className="space-y-6">
          <TabsList className="grid w-full grid-cols-3">
            <TabsTrigger value="mark">
              {currentLang === 'fr' ? 'Marquer Présence' : 'تسجيل الحضور'}
            </TabsTrigger>
            <TabsTrigger value="view">
              {currentLang === 'fr' ? 'Voir Présences' : 'عرض الحضور'}
            </TabsTrigger>
            <TabsTrigger value="reports">
              {currentLang === 'fr' ? 'Rapports' : 'التقارير'}
            </TabsTrigger>
          </TabsList>

          {/* Mark Attendance Tab */}
          <TabsContent value="mark" className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle>
                  {currentLang === 'fr' ? 'Marquer les Présences' : 'تسجيل الحضور'}
                </CardTitle>
                <CardDescription>
                  {currentLang === 'fr' 
                    ? `Marquer les présences pour le ${new Date(selectedDate).toLocaleDateString('fr-FR')}`
                    : `تسجيل الحضور لتاريخ ${new Date(selectedDate).toLocaleDateString('ar-MA')}`
                  }
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {filteredStudents.map((student) => (
                    <div key={student.id} className="flex items-center justify-between p-4 border rounded-lg">
                      <div className="flex items-center space-x-4">
                        <Checkbox
                          checked={attendanceForm[student.id]?.present ?? true}
                          onCheckedChange={(checked) => 
                            handleAttendanceChange(student.id, 'present', checked as boolean)
                          }
                        />
                        <div>
                          <p className="font-medium text-gray-800">{student.fullName}</p>
                          <p className="text-sm text-gray-600">
                            {student.level.toUpperCase()} - {student.cin}
                          </p>
                        </div>
                      </div>
                      
                      <div className="flex items-center space-x-4">
                        <Badge 
                          variant={attendanceForm[student.id]?.present ? "default" : "destructive"}
                          className={attendanceForm[student.id]?.present ? "bg-green-100 text-green-800" : ""}
                        >
                          {attendanceForm[student.id]?.present 
                            ? (currentLang === 'fr' ? 'Présent' : 'حاضر')
                            : (currentLang === 'fr' ? 'Absent' : 'غائب')
                          }
                        </Badge>
                        
                        {!attendanceForm[student.id]?.present && (
                          <Input
                            placeholder={currentLang === 'fr' ? 'Raison de l\'absence' : 'سبب الغياب'}
                            value={attendanceForm[student.id]?.reason || ''}
                            onChange={(e) => 
                              handleAttendanceChange(student.id, 'reason', e.target.value)
                            }
                            className="w-48"
                          />
                        )}
                      </div>
                    </div>
                  ))}
                </div>

                <div className="mt-6 flex justify-end">
                  <Button 
                    onClick={handleSaveAttendance}
                    className="bg-gradient-to-r from-green-600 to-blue-600 hover:from-green-700 hover:to-blue-700"
                  >
                    {currentLang === 'fr' ? 'Enregistrer les Présences' : 'حفظ الحضور'}
                  </Button>
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          {/* View Attendance Tab */}
          <TabsContent value="view" className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle>
                  {currentLang === 'fr' ? 'Historique des Présences' : 'تاريخ الحضور'}
                </CardTitle>
                <CardDescription>
                  {currentLang === 'fr' 
                    ? `Présences du ${new Date(selectedDate).toLocaleDateString('fr-FR')}`
                    : `حضور تاريخ ${new Date(selectedDate).toLocaleDateString('ar-MA')}`
                  }
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="rounded-md border">
                  <Table>
                    <TableHeader>
                      <TableRow>
                        <TableHead>{currentLang === 'fr' ? 'Élève' : 'الطالب'}</TableHead>
                        <TableHead>{currentLang === 'fr' ? 'Niveau' : 'المستوى'}</TableHead>
                        <TableHead>{currentLang === 'fr' ? 'Statut' : 'الحالة'}</TableHead>
                        <TableHead>{currentLang === 'fr' ? 'Raison' : 'السبب'}</TableHead>
                        <TableHead>{currentLang === 'fr' ? 'Date' : 'التاريخ'}</TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {filteredAttendance.map((record) => {
                        const student = students.find(s => s.id === record.studentId)
                        return (
                          <TableRow key={record.id}>
                            <TableCell className="font-medium">{record.studentName}</TableCell>
                            <TableCell>
                              <Badge variant="outline">
                                {student?.level.toUpperCase()}
                              </Badge>
                            </TableCell>
                            <TableCell>
                              <Badge 
                                variant={record.present ? "default" : "destructive"}
                                className={record.present ? "bg-green-100 text-green-800" : ""}
                              >
                                {record.present 
                                  ? (currentLang === 'fr' ? 'Présent' : 'حاضر')
                                  : (currentLang === 'fr' ? 'Absent' : 'غائب')
                                }
                              </Badge>
                            </TableCell>
                            <TableCell>{record.reason || '-'}</TableCell>
                            <TableCell>
                              {new Date(record.date).toLocaleDateString('fr-FR')}
                            </TableCell>
                          </TableRow>
                        )
                      })}
                    </TableBody>
                  </Table>
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          {/* Reports Tab */}
          <TabsContent value="reports" className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle>
                  {currentLang === 'fr' ? 'Rapports de Présence' : 'تقارير الحضور'}
                </CardTitle>
                <CardDescription>
                  {currentLang === 'fr' ? 'Statistiques et analyses des présences' : 'إحصائيات وتحليلات الحضور'}
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="text-center py-12">
                  <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-4">
                    <span className="text-2xl">📈</span>
                  </div>
                  <h3 className="text-lg font-semibold text-gray-800 mb-2">
                    {currentLang === 'fr' ? 'Rapports en développement' : 'التقارير قيد التطوير'}
                  </h3>
                  <p className="text-gray-600">
                    {currentLang === 'fr' 
                      ? 'Les rapports détaillés seront bientôt disponibles.'
                      : 'التقارير التفصيلية ستكون متاحة قريباً.'
                    }
                  </p>
                </div>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>

        {/* Language Toggle */}
        <div className="fixed bottom-6 right-6">
          <Button
            onClick={() => setCurrentLang(currentLang === 'fr' ? 'ar' : 'fr')}
            className="bg-gradient-to-r from-green-600 to-red-600 hover:from-green-700 hover:to-red-700 text-white shadow-lg"
          >
            {currentLang === 'fr' ? 'العربية' : 'Français'}
          </Button>
        </div>
      </div>
    </div>
  )
}
